img {
    max-width: 100%;
    max-height: 100%;
}
ul {
  list-style-type: none;
  height: inherit;
  padding: 0;
  margin: 5px;
}
#deckbuilder {
  display: inline-block;
  width: 825px;
}
#filter-popup {
  width: 300px;
}
.cost-val,.range-val {
  width: 2em;
}
.pow-val {
  width: 3em;
}
.list-head,.col-header {
  background-color: gray;
}
.list-head.sort-up {
  background-color: lightcyan;
}
.list-head.sort-down {
  background-color: lightcoral;
}
.hide {
  display: none;
}
.area {
  display: inline-block;
  /* border: solid 1px; */
  height: auto;
}
#deck-import {
  width: 500px;
  height: 325px;
  resize: none;
}
#details-pane {
	position: absolute;
	/* background-color: aliceblue; */
	/* width: 450px; */
	/* font-size: 12px; */
	/* z-index: 5; */
	display: inline-block;
	height: auto;
}
.card-details {
  margin-bottom: 5px;
}
.skill-container {
  margin: 4px;
}
.card-entry {
  border: solid 1px transparent;
  cursor: default;
  font-size: 14px;
  height: 18px;
}
.card-entry:hover {
  border-color: blue;
}
.card-selected {
  border-color: red;
}
.left-area {
  width: 260px;
}
.right-area {
  width: 560px;
}
#top-area {
  height: 340px;
}
#bottom-area {
  height: 400px;
}
#bottom-area, #top-area {
  margin: 3px;
}
#preview-area {
  text-align: center;
  height: inherit;
}
#right-pane {
  position: absolute;
  height: inherit;
}
#deckview {
  height: 320px;
}
#deckcontrol {
  height: 20px;
}
#cardview {
  height: inherit;
  position: absolute;
}
#deckview, #cardview {
  white-space: nowrap;
  overflow: scroll;
}
.list li span {
  display: inline-block;
  vertical-align: top;
}
.num-col {
  width: 20px;
  text-align: center;
}
.id-col {
  width: 80px;
}
.set-col {
  width: 30px;
  text-align: center;
}
.list li span.name-col {
  width: 200px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  /* vertical-align: top; */
}
.color-col {
  width: 50px;
  text-align: center;
}
.skill-col {
  width: 250px;
}
.cost-col {
  width: 40px;
  text-align: center;
}
.type-col {
	width: 75px;
	text-align: center;
}
.range-col {
  width: 40px;
  text-align: center;
}
.power-col {
  width: 30px;
  text-align: center;
}
.supp-col {
  width: 45px;
  text-align: center;
}
#search-area-fields span {
  width: 50px;
  display: inline-block;
}
#search-area-header {
  text-align: center;
}
#search-skillkey {
  width: 4em;
}
.skill-search-field {
  width: 65%;
}
.skill-search-label {
  width: 25%;
  display: inline-block;
  /* text-align: right; */
}
